

.App {
  display: block;
  background-color: #ffffff;
  overflow: hidden;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  
  display: flex;
  flex-direction: column;
  font-size: calc(4px + 1vmin);
  font-family: Didot;
  color: #000000;
  padding: 2%;
}

.dark {
  color: #000000;
}

.light {
  color: #ffffff;
}
.Name {
  width: 20%;
  height: 10%;
}

.Navi-bar {
  position: absolute;
  top: 2%;
  width: 100%;
  height: 5%;
  padding-top: 2%;
}
.Intro {
  position: absolute;
  top: 70%;
  width: 100%;
  height: 30%;
  text-align: center;
}
.Intro-text {
  display: inline-block;
  font-size: calc(4px + 1.5vmin);
  font-family: "SF Pro Text",
      "SF Pro Icons",
      "Helvetica Neue",
      "Helvetica",
      "Arial",
      sans-serif;
  color: #000000;
  font-weight: 600;
}

.sub-page {
  margin: 5%;
}
.sub-page:first-child {
  margin-right: 20%;
}

.App-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 5%;
  line-height: 3vmin;
}
#logo {
  width: 10%;
  height: 10%;
}
.projects {
  width: 100%;
  height: 10%;
  display: flex;
  
}
.App-body{
  background-color: #000000;
}

.projects .demo-clip {
  width:60%;
  height:55%;
  margin: 5%;
  filter: grayscale(100%);
}

.shader .demo-clip  {
  width: 30%;
  height: 55%;
  filter: grayscale(0);
}

.demo-clip:hover {
  filter: grayscale(0) ;
  
}

.projects .demo-text {
  width: 65%;
  margin-left: 5%;
  padding: 4%;
 
}

.demo-text span {
  display: block;
  font-family: "SF Pro Text",
      "SF Pro Icons",
      "Helvetica Neue",
      "Helvetica",
      "Arial",
      sans-serif;
  margin: 1%;
  font-size: calc(6px + 1vmin);
}

.demo-text .demo-link, .demo-text .skillset  {
  margin-top: 7%;
}
.demo-text .title {
  font-size: calc(8px + 2.5vmin);
  font-weight: 800;
  margin-bottom: 4%;
  line-height: calc(6px + 4vmin);

}
.demo-text .sub-title {
  font-weight: 800;
  font-size: calc(2px + 1vmin);
  
}

.sub-title .dark {
  color: #1f1d1c;
}
.sub-title .light {
  color: #f3e8e8;
}
.project-label {
  width:27%;
  margin-left:2%;
  margin-top:2%;
  color:#ffffff;
}
.brief-intro {
  width:30%
}
.basic-info {
  width: 15%;
}
.basic-info {
  margin: 1%;
}
.intro, .skills {
  width: 65%;
  margin: 2%;
}

.skills .sub-title {
  display: inline-block;
  margin: 1%;
  
}
.basic-info .items {
  width:10%;
  margin:5%;
}
.content {
  font-family: "SF Pro Text",
    "SF Pro Icons",
    "Helvetica Neue",
    "Helvetica",
    "Arial",
    sans-serif;
  font-size: calc(5px + 1vmin);
}
.content, .brief-intro, .basic-info {
  text-align: center;
}



.App-link {
  color: #61dafb;
}
a:link {
  color: white;
}

/* visited link */
a:visited {
  color: white;
}





@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
